<template>
   <div id="root" class="bg-white">
    <div class="w-600px  mx-auto ">
      <div class="p-4 rounded-lg border border-gray-300">
        <HeaderAdd :addTodo="addTodo"></HeaderAdd>
        <ListTodo :todos="todos" :checkTodo="checkTodo"  :deleteTodo="deleteTodo"></ListTodo>
        <FooterItem :todos="todos" :checkAllTodo="checkAllTodo" :clearAllTodo="clearAllTodo"></FooterItem>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderAdd from './components/HeaderAdd'
import ListTodo from './components/ListTodo'
import FooterItem from './components/FooterItem'

export default {
  name: 'App',
  components: {
    HeaderAdd,
    ListTodo,
    FooterItem
  },
  data(){
    return{
      todos:[
        {id:'001',title:'吃饭',done:true},
        {id:'002',title:'睡觉',done:false},
        {id:'003',title:'打豆豆',done:true},
      ]
    }
  },
  methods:{
    addTodo(todoObj){
        this.todos.unshift(todoObj)
    },
    checkTodo(aaa){
				this.todos.forEach(todo=>{
					if(todo.id === aaa)  todo.done = !todo.done
				})
		},
    deleteTodo(id){
				this.todos = this.todos.filter( todo => todo.id !== id )
		},
    checkAllTodo(done){
      this.todos.forEach(todo=>{
				todo.done =done
      })
    },
    clearAllTodo(){
      this.todos = this.todos.filter( todo =>{
        return !todo.done 
      }) 
    }
  }
}

</script>

